import React from 'react'
import styled from 'styled-components'
import Typist from 'react-typist'

import { MediaQuery } from '../../../styles/media'

const GridContainer = styled.div`
  display: grid;
  grid-template-columns: 60vw 40vw;
  grid-template-rows: auto;
  grid-template-areas: 'lefty righty';
  justify-items: center;
  ${MediaQuery.xs`
    grid-template-columns: 70vw 30vw;
  `}
`

const GridLeft = styled.div`
  grid-area: lefty;
  place-self: center;
`

const GridRight = styled.div`
  grid-area: righty;
  place-self: center;
`

const StyledTypist = styled(Typist)`
  // font-family: 'Exo 2';
  font-family: 'Nulshock';
  color: #fff;
  letter-spacing: 17px;
  font-size: 4em;
  font-weight: 200;
  font-style: normal;
  font-stretch: 100%;
  letter-spacing: 0.2em;
  text-size-adjust: 100%;
  line-height: 1.1em;
  text-transform: uppercase;
  text-shadow: 5px 10px 10px rgba(0, 0, 0, 0.75),
    -3px 10px 12px rgba(0, 0, 0, 0.75);
  margin: 0 auto;
  text-align: left;
  -webkit-font-smoothing: antialiased;
  ${MediaQuery.xs`
    font-size: 3em;
    letter-spacing: normal;
  `}
`

const StyledTitle = styled(Typist)`
  font-size: 1.2em;
  color: #fff;
  text-transform: uppercase;
  margin: 40px auto 0 10px;
  text-align: left;
  text-shadow: 5px 10px 10px rgba(0, 0, 0, 0.75),
    -3px 10px 12px rgba(0, 0, 0, 0.75);
  -webkit-font-smoothing: antialiased;
`

class HeroText extends React.Component {
  render() {
    if (this.props.text) {
      return (
        <StyledTypist cursor={{ show: false }} {...this.props}>
          <strong>{this.props.text.split(' ').slice(0, 1)}</strong>
          <br />
          {this.props.text
            .split(' ')
            .slice(1)
            .join(' ')}
        </StyledTypist>
      )
    } else {
      return (
        <GridContainer>
          <GridLeft>
            <StyledTypist cursor={{ show: false }} {...this.props}>
              Listen. <br />
              Disrupt. <br />
              Learn.
            </StyledTypist>
            <StyledTitle cursor={{ show: false }} {...this.props}>
              <Typist.Delay ms={2000} />
              Matt Ortiz - Creative Leader
            </StyledTitle>
          </GridLeft>
          <GridRight />
        </GridContainer>
      )
    }
  }
}

export default HeroText
