import React from 'react'
import styled from 'styled-components'

import { Link } from 'gatsby'
import siteLogo from '../../images/envyforge-logo-white.png'
import { MediaQuery } from '../../styles/media'

const HeaderWrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1; // keeps the header on top of any content
  width: 100vw;
  min-width: 375px; // iPhone 6
  // grid-area: header;
  height: auto;
  font-size: 1.4em;
  margin: 0;
  img {
    margin-bottom: 0;
    height: 32px;
    padding-top: 2px;
  }
`

const HeaderBorder = styled.div`
  height: 4px;
  width: 100vw;
  margin: 0;
  background: rgb(0, 97, 158);
  // background: rgb(24, 53, 88);
`

const HeaderContainer = styled.div`
  display: flex;
  flex-wrap: no-wrap;
  padding: 10px 0 10px 10px;
  margin: 0;
  justify-content: space-between;
  // background: rgba(0, 97, 158, 0.8); // blue 
  background: rgba(0, 0, 0, 0.6); // blue 
  // ${MediaQuery.sm`
  //   top: 69px;
  //   background: rgba(0, 97, 158, 1); // white
  // `}
`

const HeaderLeft = styled.div`
  grid-column: 1 / 1;
`

const HeaderRight = styled.div`
  grid-column: 3 / 3;
  padding-right: 10px;
`

const Header = () => (
  <HeaderWrapper>
    <HeaderBorder />
    <HeaderContainer>
      <HeaderLeft>
        <Link to="/">
          <img src={siteLogo} alt="Matt Ortiz Logo" />
        </Link>
      </HeaderLeft>
      <HeaderRight>{/* <ActionBar /> */}</HeaderRight>
    </HeaderContainer>
  </HeaderWrapper>
)

export default Header
